<template>
  <div class="wrapper" @click.self="close()">
    <div class="bgc">
      <div class="container">
        <div class="header">
          <div class="top">
            <label> </label>
            <btns-save-close @close="close" @save="save">
              <template v-slot:close></template>
            </btns-save-close>
          </div>
          <br />
          <div class="bottom">
            <div class="permits">
              <div class="permit">
                <input
                  type="checkbox"
                  class="checkbox"
                  id="q1"
                  v-model="copyConfing.allow_add_reserve"
                />
                <label for="q1">
                         
                </label>
              </div>
              <div class="permit">
                <input
                  type="checkbox"
                  class="checkbox"
                  id="q2"
                  v-model="copyConfing.allow_change_price"
                />
                <label for="q2">
                         ( )
                </label>
              </div>
              <div class="permit">
                <input
                  type="checkbox"
                  class="checkbox"
                  id="q3"
                  v-model="copyConfing.allow_add_with_zero_count"
                />
                <label for="q3">
                         
                </label>
              </div>
            </div>
            <div class="sync">
              <label>      amoCRM</label>
              <div class="list">
                <div class="label_input">
                  <label>
                        amoCRM  
                  </label>
                  <SelectorVue
                    :options_props="copyProductLists"
                    @select="
                      (event) => optionSelectSync(event, 'amo_product_list')
                    "
                    :selected_option="copyProductLists[0]"
                  />
                </div>
                <div class="label_input">
                  <label>
                       amoCRM  .   
                            , 
                          
                    .
                  </label>
                  <SelectorVue
                    :options_props="amoLeadsGroupHide"
                    @select="
                      (event) => optionSelectSync(event, 'amo_leads_group_hide')
                    "
                    :selected_option="selectedAmoLeadsGroupHide"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="content">
          <h6>    </h6>
          <div class="small">
            !        
              .     , 
                    .
          </div>
          <div class="steps">
            <div
              class="label_input"
              v-for="item in copyPipelinesList"
              :key="item"
            >
              <label>  "{{ item?.name }}" </label>
              <SelectorVue
                :options_props="item?.statuses"
                @select="
                  (event) =>
                    optionSelectSteps(event, item?.value, 'reserve_off')
                "
                :selected_option="item.selectedReserveOff"
              />
            </div>
          </div>
          <h6>    </h6>
          <div class="small">
            !         
             .
          </div>
          <div class="steps">
            <div
              class="label_input"
              v-for="item in copyPipelinesList"
              :key="item"
            >
              <label>  "{{ item?.name }}" </label>
              <SelectorVue
                :options_props="item?.statuses"
                @select="
                  (event) => optionSelectSteps(event, item?.value, 'write_off')
                "
                :selected_option="item.selectedWriteOff"
              />
            </div>
          </div>
          <h6>  </h6>
          <div class="steps">
            <div class="label_input" v-for="item in leadsDeals" :key="item">
              <label>  "{{ item?.name }}" </label>
              <SelectorVue
                :options_props="leadsDealsList"
                @select="(event) => optionSelectLeadsDeals(event, item?.code)"
                :selected_option="item?.selected"
              />
            </div>
          </div>
        </div>
        <div class="footer">
          <btns-save-close @close="close" @save="save">
            <template v-slot:close></template>
          </btns-save-close>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SelectorVue from "@/components/SelectorVue";
import BtnsSaveClose from "@/components/BtnsSaveClose.vue";
export default {
  components: {
    SelectorVue,
    BtnsSaveClose,
  },
  data() {
    return {
      copyConfing: {
        reserve_off: [],
        write_off: [],
      },
      copyPipelinesList: [],
      copyLeadFieldsList: [],
      leadsDeals: [
        {
          name: "",
          code: "field_products",
          selected: { name: " ", value: -1 },
        },
        {
          name: "",
          code: "field_cost_price",
          selected: { name: " ", value: -1 },
        },
        {
          name: "",
          code: "field_profit",
          selected: { name: " ", value: -1 },
        },

        {
          name: " ",
          code: "field_budget",
          selected: { name: " ", value: -1 },
        },

        {
          name: " ",
          code: "field_nds_sum",
          selected: { name: " ", value: -1 },
        },
      ],
      leadsDealsList: [],
      copyProductLists: [],
      amoLeadsGroupHide: [],
      selectedAmoLeadsGroupHide: { name: " ", value: -1 },
    };
  },
  async mounted() {
    await this.$store.dispatch("get_account");
    this.copyConfing = this.$store.state.account.account.config;
    await this.$store.dispatch("getPipelinesList");
    this.copyPipelinesList = this.pipelinesList;
    await this.$store.dispatch("getLeadFieldsList");
    this.copyLeadFieldsList = this.leadFieldsList;
    await this.$store.dispatch("getProductLists");
    this.copyProductLists = this.productLists;
    this.searchSelectedPipelines();
    this.feelLeadsDealsList();
    this.feelAmoLeadsGroupHide();
  },
  computed: {
    pipelinesList() {
      const list = [];
      Object.entries(this.$store.state.account.pipelinesList).map((val) => {
        const arr = [];
        Object.entries(val[1].statuses).forEach((stat) =>
          arr.push({ name: stat[1], value: stat[0] })
        );
        val[1].statuses = arr;
        val[1].statuses.unshift({ name: " ", value: -1 });
        val[1].selectedReserveOff = { name: " ", value: -1 };
        val[1].selectedWriteOff = { name: " ", value: -1 };
        list.push({ value: val[0], ...val[1] });
      });
      return list;
    },
    leadFieldsList() {
      const list = [];
      Object.entries(this.$store.state.account.leadFieldsList).map((val) => {
        const arr = [];
        Object.entries(val[1].fields).forEach((stat) =>
          arr.push({ name: stat[1], value: stat[0] })
        );
        val[1].fields = arr;
        val[1].fields.unshift({ name: " ", value: -1 });
        val[1].selected = { name: " ", value: -1 };
        list.push({ value: val[0], ...val[1] });
      });
      return list;
    },
    productLists() {
      return [
        ...this.$store.state.account.productLists,
        { name: " ", value: -1 },
      ];
    },
  },
  methods: {
    save() {
      this.$store.dispatch("update_account", this.copyConfing);
      this.close();
    },
    close() {
      this.$store.commit("open_close_show_edit_stuff", false);
    },
    optionSelectLeadsDeals(option, code) {
      if (option.value == -1) {
        this.copyConfing[code] = 0;
      } else {
        this.copyConfing[code] = option.value;
      }
      this.leadsDeals.map((val) => {
        if (val.code == code) val.selected = option;
      });
    },
    optionSelectSync(option, code) {
      if (option.value == -1) {
        this.copyConfing[code] = 0;
      } else {
        if (code == "amo_leads_group_hide")
          this.selectedAmoLeadsGroupHide = option;
        this.copyConfing[code] = option.value;
      }
    },
    optionSelectSteps(option, value, code) {
      let addNew = true;
      if (this.copyConfing[code][value]) {
        this.copyConfing[code][value] = option.value;
        addNew = false;
      }

      const obj = {};
      obj[value] = option.value;
      if (addNew) this.copyConfing[code].push(obj);

      if (option.value == -1) {
        this.copyConfing[code].forEach((val, idx) => {
          if (Object.entries(val)[0][0] == value)
            delete this.copyConfing[code][idx];
        });
      }

      this.copyPipelinesList.map((val) => {
        if (val.value == value) {
          if (code == "write_off") {
            val.selectedWriteOff = option;
          } else {
            val.selectedReserveOff = option;
          }
        }
      });
    },
    searchSelectedInArr(item, arr, code) {
      let res = { name: " ", value: -1 };
      arr.forEach((val) => {
        if (val[code] == item) res = val;
      });
      return res;
    },
    searchSelectedPipelines() {
      this.copyPipelinesList.map((val) => {
        if (this.copyConfing.reserve_off[val.value])
          val.selectedReserveOff = this.searchSelectedInArr(
            this.copyConfing.reserve_off[val.value],
            val.statuses,
            "value"
          );
        if (this.copyConfing.write_off[val.value])
          val.selectedWriteOff = this.searchSelectedInArr(
            this.copyConfing.write_off[val.value],
            val.statuses,
            "value"
          );
      });
    },
    feelLeadsDealsList() {
      this.leadsDealsList.push({ name: " ", value: -1 });
      this.copyLeadFieldsList.forEach((val) => {
        const optgroup = val.name;
        this.leadsDealsList.push({ name: optgroup, value: "optgroup" });
        const list = val.fields.splice(1, 999);
        list.forEach((item) =>
          this.leadsDealsList.push({ ...item, optgroup: true })
        );
      });
    },
    feelAmoLeadsGroupHide() {
      this.amoLeadsGroupHide.push({ name: " ", value: -1 });
      this.copyLeadFieldsList.forEach((val) => {
        this.amoLeadsGroupHide.push({ name: val.name, value: val.value });
      });
    },
    searchSelectedAmoLeadsGroupHide() {
      this.amoLeadsGroupHide.forEach((val) => {
        if (val.value == this.copyConfing.amo_leads_group_hide)
          this.selectedAmoLeadsGroupHide = val;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/app.scss";
.wrapper {
  pointer-events: all;
  z-index: 9999999;
  width: 100%;
  height: max-content;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: transparent;
  .bgc {
    width: 80%;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 0.3rem;
    margin: 30px auto;
    outline: 0;
    box-shadow: 0 0 7px 6px rgb(206 212 218 / 50%);
    .container {
      text-align: left;
      width: 80%;
      margin: 30px auto;
      display: flex;
      flex-direction: column;
      .header {
        display: flex;
        flex-direction: column;
        justify-content: start;
        .top {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          border-bottom: 1px solid #c9c9c9;
          padding-bottom: 10px;
          label {
            @include font(500, 28px);
          }
        }
        .bottom {
          display: flex;
          flex-direction: column;
          gap: 16px;
          .permits {
            display: flex;
            flex-direction: column;
            gap: 10px;
            .permit {
              label {
                @include font(400, 16px);
              }
            }
          }
          .sync {
            label {
              @include font(500, 16px);
            }
            .list {
              padding: 15px;
              border: 1px solid #c9c9c9;
              border-radius: 5px;
              margin-top: 10px;
              display: flex;
              flex-direction: column;
              gap: 10px;
              .label_input {
                display: flex;
                flex-direction: column;
                gap: 5px;
                label {
                  @include font(400, 14px);
                }

                .v-select {
                  width: calc(100% - 26px) !important;
                  margin-right: 24px;
                  :deep(.title) {
                    width: 100% !important;
                  }
                  :deep(.options) {
                    width: calc(100% + 24px) !important;
                    text-align: left;
                    p {
                      width: calc(100% - 24px) !important;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .content {
        @include font(400, 16px);
        h6 {
          @include font(500, 16px);
        }
        .small {
          @include font(400, 14px);
        }
        .steps {
          display: flex;
          flex-direction: column;
          border: 1px solid #c9c9c9;
          border-radius: 4px;
          margin-top: 30px;
          padding: 10px;
          .label_input {
            display: flex;
            flex-direction: row;
            padding: 10px;
            label {
              width: 40%;
            }
            .v-select {
              width: calc(50% - 26px);
              margin-right: 24px;
              :deep(.title) {
                width: 100%;
              }
              :deep(.options) {
                width: calc(100% + 24px);
                text-align: left;
                p {
                  width: calc(100% - 24px);
                }
              }
            }
          }
        }
      }
      .footer {
        display: flex;
        justify-content: end;
        margin-top: 40px;
      }
    }
  }
}
.checkbox:disabled + label::before {
  background-color: #e9ecef;
  border-color: #b3d7ff;
  cursor: default;
}
</style>
